.wrapper {
    padding: 2% 1% 0 1%;
    margin-bottom: 4%;
    display: flex;
    column-gap: 1%;
    align-items: normal;
}

.avatarImg {
    width: 120px;
    height: 120px;
    border-radius: 20% 50% 20% 50%;
}


.uploadFileInput {
    opacity: 0;
    position: absolute;
    z-index: -1;
    overflow: hidden;
    width: 0.4px;
    height: 0.4px;
}

.uploadFileLabel {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px dashed #150B22;
    padding: 1% 5%;
    border-radius: 6px;
    cursor: pointer;
}

.uploadFileIcon {
    width: 15px;
    height: auto;
}

.uploadFileLabel .uploadFileText,
.uploadFileLabel .uploadFileIcon path {
    transition: .25s ease;
    font-size: 11px;
}

.uploadFileLabel:hover .uploadFileText {
    color: aliceblue;
}

.uploadFileLabel:hover .uploadFileIcon path {
    fill: aliceblue;
}

.uploadFileLabel:hover {
    border: 2px dashed aliceblue;
}